import { useState } from "react";
// import { Redirect, useHistory, Link } from "react-router-dom";
import { Col, Row, Typography, notification } from "antd";
import { Navigate, useNavigate, Link } from "react-router-dom";

import RegisterForm from "./RegisterForm";

import { useTranslation } from "react-i18next";
import loginProp from "shared/assets/images/loginPageImg.png";
import "./Register.scss";

import API_SERVICE from "client/src/services/api-service";
import { AuthConsumer } from "shared/contexts/AuthContext";
import { LOGIN, OTP } from "../../constants/RouteConstants";

const { Title } = Typography;

interface Props {
  location: any;
}

export default function Register({ location }: Props) {
  const navigate = useNavigate();

  const { t } = useTranslation();
  const [loading, setLoading] = useState(false);
  // const { from } = location.state || { from: { pathname: "/" } };

  return (
    <AuthConsumer>
      {({ isAuth, updateAuthToken }) => {
        return isAuth ? (
          <Navigate to={"/"} replace={true} />
        ) : (
          <Row className="login-form-row  ">
            <Col
              sm={24}
              md={10}
              lg={12}
              className={"login-form-wrapper-column "}
            >
              <div className="login-form-wrapper" style={{ width: "100%" }}>
                {/* <div className="logo">
                  <img src={"https://random.imagecdn.app/500/150"} alt="logo" />
                </div> */}
                <div className="login-title">ALTOVA CLOUD PORTAL SIGN-UP</div>
                <p className="login-text">
                  Please provide your name, e-mail address and password that you
                  want to use to access the Altova Cloud Portal
                </p>
                <RegisterForm
                  onRegister={async (
                    email: string,
                    name: string,
                    role: string,
                    phoneNumber: string,
                    cNumber: string,
                    cName: string,
                    incorporationDate: string,
                    employeesNumber: string,
                    isSME: boolean
                  ) => {
                    setLoading(true);
                    try {
                      const {
                        data: { data },
                      } = await API_SERVICE.register(
                        email,
                        name,
                        role,
                        phoneNumber,
                        cNumber,
                        cName,
                        incorporationDate,
                        employeesNumber,
                        isSME
                      );
                      notification.success({
                        message: "Company Registered",
                        placement: "bottomRight",
                      });
                      setLoading(false);
                      navigate(OTP);
                    } catch (e) {
                      notification.error({
                        message: t(API_SERVICE.handleErrors(e)),
                        placement: "bottomRight",
                      });
                      setLoading(false);
                    }
                  }}
                  loading={loading}
                />

                <div className="mt-2 mb-2 signUpText">
                  <h2 onClick={() => navigate(LOGIN)}>Login Now</h2>
                </div>
              </div>
            </Col>
            <Col sm={24} md={10} lg={12} className="login-text-column">
              <div className="login-text-wrapper">
                <img
                  src={loginProp}
                  alt="login"
                  className="img-fluid login-prop-image"
                />
              </div>
            </Col>
          </Row>
        );
      }}
    </AuthConsumer>
  );
}
