import React, { useState } from "react";
import {
  Form,
  Input,
  Button,
  Row,
  Col,
  Radio,
  Checkbox,
  DatePicker,
  notification,
} from "antd";
import { useTranslation } from "react-i18next";
import moment from "moment";
import API_SERVICE from "client/src/services/api-service";

interface Props {
  onRegister: (
    email: string,
    name: string,
    role: string,
    phoneNumber: string,
    cNumber: string,
    cName: string,
    incorporationDate: string,
    employeesNumber: string,
    isSME: boolean
  ) => void;
  loading: boolean;
}

export default function RegisterForm({ onRegister, loading }: Props) {
  const { t } = useTranslation();
  const [form] = Form.useForm();

  const [isChecked, setChecked] = useState(false);

  const onCheckboxChange = async (e: any) => {
    await setChecked(e.target.checked);
  };

  function handleSubmit(values: any) {
    if (onRegister) {
      const {
        email,
        name,
        role,
        phoneNumber,
        cNumber,
        cName,
        incorporationDate,
        employeesNumber,
        isSME,
      } = values;
      onRegister(
        email,
        name,
        role,
        phoneNumber,
        cNumber,
        cName,
        incorporationDate.format("YYYY-MM-DD"),
        employeesNumber,
        isSME
      );
    }
  }

  const fetchCompanyHouseDetails = async (e: any) => {
    let cin = e.target.value;

    if (cin) {
      try {
        const {
          data: { data },
        } = await API_SERVICE.getCompanyHouseDetails(cin);
        form.setFieldsValue({
          ["cName"]: data.name,
          ["incorporationDate"]: moment(data.incorporationDate),
        });
      } catch (e) {
        notification.error({
          message: API_SERVICE.handleErrors(e),
          placement: "bottomRight",
        });
      }
    }
  };

  /*React.useEffect(()=>{
      console.log(isChecked)
    },[isChecked])*/

  function disabledDate(current: any) {
    return current && current > moment().endOf("day");
  }

  return (
    <Form
      // className={"register-form"}
      className={"login-form"}
      form={form}
      layout="vertical"
      onFinish={handleSubmit}
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 20 }}
      requiredMark={false}
    >
      <Form.Item
        label={"First Name"}
        name="firstName"
        rules={[{ required: true, message: "First Name required" }]}
      >
        <Input
          autoComplete={"off"}
          className="login-input"
          placeholder="Enter your First Name"
        />
      </Form.Item>
      <Form.Item
        label={"Last Name"}
        name="lastName"
        rules={[{ required: true, message: "Last Name required" }]}
      >
        <Input
          autoComplete={"off"}
          className="login-input"
          placeholder="Enter your Last Name"
        />
      </Form.Item>
      <Form.Item
        label={"Email ID"}
        name="email"
        rules={[{ required: true, type: "email", message: "Email required" }]}
      >
        <Input
          autoComplete={"off"}
          className="login-input"
          placeholder="Enter your  Email ID"
        />
      </Form.Item>

      <Form.Item
        name="password"
        label="Password"
        rules={[
          {
            required: true,
            message: "Please input your password!",
          },
        ]}
        hasFeedback
      >
        <Input.Password placeholder="Enter Password" className="login-input" />
      </Form.Item>

      <Form.Item
        name="confirm"
        label="Confirm Password"
        dependencies={["password"]}
        hasFeedback
        rules={[
          {
            required: true,
            message: "Please confirm your password!",
          },
          ({ getFieldValue }) => ({
            validator(_, value) {
              if (!value || getFieldValue("password") === value) {
                return Promise.resolve();
              }
              return Promise.reject(
                new Error("The two passwords that you entered do not match!")
              );
            },
          }),
        ]}
      >
        <Input.Password
          placeholder="Confirm Password"
          className="login-input"
        />
      </Form.Item>
      {/* <Form.Item
        label={""}
        name="consent"
        valuePropName="checked"
        rules={[
          {
            validator: (_, value) =>
              value
                ? Promise.resolve()
                : Promise.reject(new Error("Should accept Terms & Conditions")),
          },
        ]}
      >
        <Checkbox checked={isChecked} onChange={onCheckboxChange}>
          I accept the{" "}
          <a href="" className="theme-link">
            Terms & Conditions.
          </a>
        </Checkbox>
      </Form.Item> */}

      <br />

      <Form.Item className="mb-3">
        <Button
          type="primary"
          htmlType="submit"
          block
          loading={loading}
          className="theme-button primary reset-btn"
        >
          Register Now
        </Button>
      </Form.Item>

      {/* <Row justify={"end"}>
        <Col sm={18} className={"text-center"}>
          or
        </Col>
      </Row> */}
    </Form>
  );
}
