import { Col, Row, Typography, notification } from "antd";
import { useEffect, useState } from "react";
import { Navigate, Link, useNavigate } from "react-router-dom";
import LoginForm from "./LoginForm";

import { useTranslation } from "react-i18next";
import "./Login.scss";

import loginProp from "shared/assets/images/loginPageImg.png";
import { REGISTER } from "shared/constants/RouteConstants";

import API_SERVICE from "client/src/services/api-service";
import { AuthConsumer } from "shared/contexts/AuthContext";
import React from "react";

const { Title } = Typography;

interface Props {
  location: any;
}

export default function Login({ location }: Props) {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(false);
  console.log(location);

  // const { from } = location.state || { from: { pathname: "/" } };
  const navigate = useNavigate();

  useEffect(() => {
    document.title = "Login Page";
  }, []);

  return (
    <AuthConsumer>
      {({ isAuth, user, updateAuthToken }) => {
        return isAuth ? (
          <Navigate to={"/admin"} replace={true} />
        ) : (
          <Row className="login-form-row  ">
            <Col
              sm={24}
              md={10}
              lg={12}
              className={"login-form-wrapper-column "}
            >
              <div className="login-form-wrapper" style={{ width: "100%" }}>
                {/* <div className="logo">
                  <img src={"https://random.imagecdn.app/500/150"} alt="logo" />
                </div> */}
                <div className="login-title">Login</div>
                <p className="login-text">
                  Already have an account? Please Login!
                </p>
                <LoginForm
                  onLogin={async (email: string, password: string) => {
                    setLoading(true);
                    try {
                      const {
                        data: { data },
                      } = await API_SERVICE.login(email, password);
                      updateAuthToken(data);
                    } catch (e) {
                      notification.error({
                        message: t(API_SERVICE.handleErrors(e)),
                        placement: "bottomRight",
                      });
                      setLoading(false);
                    }
                  }}
                  loading={loading}
                />
                <div className="mt-2 mb-2 signUpText">
                  <h5 onClick={() => navigate(REGISTER)}>
                    Sign up as new user
                  </h5>
                </div>
              </div>
            </Col>
            <Col sm={24} md={10} lg={12} className="login-text-column">
              <div className="login-text-wrapper">
                <img
                  src={loginProp}
                  alt="login"
                  className="img-fluid login-prop-image"
                />
              </div>
            </Col>
          </Row>
        );
      }}
    </AuthConsumer>
  );
}
