import { Button, Checkbox, Col, Form, Input, Row } from "antd";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";

import { FORGOT_PASSWORD } from "../../../shared/constants/RouteConstants";
import eye from "../../assets/icons/eye.png";
import lockIcon from "../../assets/icons/lockIcon.png";
import username from "../../assets/icons/username.png";
import React from "react";
// import ForgotPass from "../../view/forgotPass/forgotPass.tsx";

interface Props {
  onLogin: (email: string, password: string) => void;
  loading: boolean;
}

export default function LoginForm({ onLogin, loading }: Props) {
  const { t } = useTranslation();
  const [form] = Form.useForm();
  const navigate = useNavigate();
  const onChange = (e) => {
    console.log(`checked = ${e.target.checked}`);
  };

  function handleSubmit(values: any) {
    if (onLogin) {
      const { email, password } = values;
      onLogin(email, password);
    }
  }

  return (
    <>
      <Form
        className={"login-form"}
        form={form}
        layout="vertical"
        fields={[]}
        initialValues={{
          email: "",
          password: "",
        }}
        onFinish={handleSubmit}
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 20 }}
        requiredMark={false}
      >
        <Form.Item
          className={"mb-3"}
          label={"Email"}
          name="email"
          rules={[{ required: true, message: "Email required" }]}
        >
          <Input
            autoComplete={"off"}
            size="large"
            placeholder="username/email"
            className="login-input"
            prefix={<img src={username} />}
          />
        </Form.Item>
        <Form.Item
          className={"mb-3"}
          label={"Password"}
          name="password"
          rules={[{ required: true, message: "Password required" }]}
        >
          <Input.Password
            autoComplete={"off"}
            size="large"
            placeholder="password"
            prefix={<img src={lockIcon} />}
            type="password"
            className="login-input"
            suffix={<img src={eye} />}
          />
        </Form.Item>

        <Form.Item className="mb-3">
          <Button
            type="primary"
            htmlType="submit"
            block
            loading={loading}
            className="theme-button primary reset-btn"
          >
            Sign in
          </Button>
        </Form.Item>

        <Row>
          <Col xs={12} lg={8} xl={15} xxl={11}>
            <div className="form-text">
              <small>
                <Checkbox onChange={onChange}>Remember Me</Checkbox>
              </small>
            </div>
          </Col>
          <Col xs={12} lg={6} xl={5} xxl={5} className="forgot-pass p-0 m-0">
            <small>
              <a
                className="text-primary"
                onClick={() => {
                  navigate(FORGOT_PASSWORD);
                }}
              >
                Forgot Password?
              </a>
            </small>
          </Col>
        </Row>
      </Form>
    </>
  );
}
